

.CodeNames {
    border-radius: 5px;
    text-align: center;
    background: -webkit-gradient(linear,left top, right top,from(#6945C6),to(#747194));
    background: -webkit-linear-gradient(left,#6945C6,#747194);
    background: -o-linear-gradient(left,#6945C6,#747194);
    background: linear-gradient(to right,#6945C6,#747194);
    -webkit-box-shadow: 0px 2px 5px rgba(0,0,0,0.12), 0px 2px 5px rgba(0,0,0,0.24);
            box-shadow: 0px 2px 5px rgba(0,0,0,0.12), 0px 2px 5px rgba(0,0,0,0.24);
    color: #333;
    margin: 0px auto;
    padding: 20px;
    width: 90%;
    margin-top: 40px;
    color:white;
  }

  span{
    font-weight: bold;
    font-size: 18px;

  }

  #evidence{

    background: #F09310 ;
    padding: 6px 12px 6px 12px;
  }



