

.GenerateCodeName
 {
  
    text-align: center;
    width: 90%;
    margin: 0px auto;
  }

  form{

    max-width: 90%;
    margin: 0px auto;
  }

  input{
    margin: 10px 5px 10px 10px;
    border: none;
    border-radius: 6px; 
    background: rgba(116,113,148, 0.4);
    max-width:250px;
    height: 30px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    color: white;
    font-family: 'Montserrat', sans-serif;
    font-weight: bold;
    padding: 2px 2px 2px 12px;
    font-size: 18px;
    

  } 


  input:hover{

    -webkit-box-sizing: border-box;

            box-sizing: border-box;
    -webkit-box-shadow: 0px 3px 15px rgba(0,0,0,0.5);
            box-shadow: 0px 3px 15px rgba(0,0,0,0.5);
  }
  input:focus {
    outline: none;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
-webkit-box-shadow: 0px 3px 15px rgba(0,0,0,0.5);
        box-shadow: 0px 3px 15px rgba(0,0,0,0.5);
 }

  form{

    display: -ms-flexbox;

    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
  }

  button{
    
    background: -webkit-gradient(linear ,left top, left bottom ,from(#38B2FB) ,color-stop(#32CAFE),to(#38B2FB) );
    
    background: -webkit-linear-gradient(#38B2FB ,#32CAFE,#38B2FB );
    
    background: -o-linear-gradient(#38B2FB ,#32CAFE,#38B2FB );
    
    background: linear-gradient(#38B2FB ,#32CAFE,#38B2FB );
    border: none;
    -webkit-box-shadow: 0px 5px 13px rgba(0,0,0,0.2);
            box-shadow: 0px 5px 13px rgba(0,0,0,0.2);
    border-radius: 4px;  
    color: white;
    padding: 6px 10px 6px 10px;
    font-family: 'Roboto', sans-serif;
    font-size: 18px;
    text-align: center;
    margin: 0px auto;
    margin-top: 20px;
    height: 50px;
    -webkit-transition:all 0.3s cubic-bezier(.25,.8,.25,1);
    -o-transition:all 0.3s cubic-bezier(.25,.8,.25,1);
    transition:all 0.3s cubic-bezier(.25,.8,.25,1);
    outline:none;
    cursor: pointer;

  } 
  button:hover{

     -webkit-box-shadow: 0px 5px 13px rgba(0,0,0,0.7);

             box-shadow: 0px 5px 13px rgba(0,0,0,0.7);
   


  }

  label{
    padding-top:3px;
    color: white;


  }

  @media(max-width:370px){
    input{width:100%;}
 

  }


