.Dashboard {
   
    padding: 20px;
    border-radius: 12px;
    background: -webkit-gradient(linear,left top, right top,from(#1E1740), color-stop(#2E2753),to(#1E1740));
    background: -webkit-linear-gradient(left,#1E1740, #2E2753,#1E1740);
    background: -o-linear-gradient(left,#1E1740, #2E2753,#1E1740);
    background: linear-gradient(to right,#1E1740, #2E2753,#1E1740);
    max-width: 700px;
    min-height: 400px;
    margin: 0px auto;
    margin-top: 50px;
    margin-bottom: 50px;
    -webkit-box-shadow: 0 2px 5px rgba(0,0,0,0.12), 0 2px 5px rgba(0,0,0,0.24);
            box-shadow: 0 2px 5px rgba(0,0,0,0.12), 0 2px 5px rgba(0,0,0,0.24);
    -webkit-transition: all 0.3s cubic-bezier(.25,.8,.25,1);
    -o-transition: all 0.3s cubic-bezier(.25,.8,.25,1);
    transition: all 0.3s cubic-bezier(.25,.8,.25,1);
    position: relative;
  }
  
  
  
  .Dashboard:hover {
    -webkit-box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
            box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
    
    
  }


